<template>
 <v-touch class="detail"  :style="styleObj" @tap='tap'  @swipeleft="swipeleft" @swiperight="swiperight" @swipedown="down"></v-touch>
</template>
<script>
export default {
    data(){
        return{
             nowindex:0,
        }
    },
    mounted(){
         this.nowindex = this.$route.params.index
         console.log('图片下标'+ this.nowindex);

             //   console.log(this.$route.params.index);//接收route传参

          console.log('vuex'+this.$store.state.photoList); //vuex 输出    
          console.log(this.$store.state.photoList[this.$route.params.index]);
        console.log('地址'+this.$store.state.photoList[this.$route.params.index].url);
    },
     computed: {
        styleObj() {
          return {
                background: `#000 url(${this.$store.state.photoList[this.nowindex].url}) no-repeat center / contain`,
            };
           
        }
    },
    methods:{
        swipeleft(){
            this.nowindex++;
            if(this.nowindex==this.$store.state.photoList.length){
                this.nowindex=0;
            }
            console.log('left')
        },
        swiperight(){
            this.nowindex--;
            if(this.nowindex<0){
                 this.nowindex = this.$store.state.photoList.length - 1;
            }
            console.log('right')
        },
        down(){
            console.log('down')
        },
         tap(){
            console.log('tap')
             this.$router.go(-1);
        }
    }
}
</script>
<style scoped>
.detail{margin:0.65rem 0; position:absolute; top:0; right:0; bottom:0; left:0; height:500px;}
</style>



