</template>

<script>
    export default{
        props:['bg'],
    }
</script>

<style scoped>
    .footer{
        width: 100%;
        height: 1rem;
        background: #2196f3;
        position: fixed;
        bottom: 0;
        line-height: 1rem;
        display: flex;
        justify-content: space-evenly;
        color: #fff;
       
    }
    .footer li a{
        color:#ccc;
        outline:none;
    }
    .footer li a.router-link-active{
        color:#fff;
    }
</style>


