<template>
    <div id="book">
        <div class="serch">
            <input type="text" id="input" placeholder="请输入书籍名称" v-model="name">
            <img src="./images/搜索.svg" @click="show">
        </div>
        <div class="book-list" v-for="list in booklist" :key ="list.bid">
            <dl>
                <dt>
                    <img :src="list.book_cover" v-show="list.book_cover">
                    <img :src="list.cover"> 
                    <!-- 搜索出来的 -->
                </dt>
                <dd>
                    <p>{{list.bookname}}{{list.title}}</p>
                    <div class="xzzf">
                        <p class="book-size">{{list.book_info}}{{list.desc}}</p>
                    </div>
                    <span class="woker">{{list.author_name}}{{list.author}}</span>
                    <span class="lz" v-show="list.stat_name">{{list.stat_name}}</span>
                    <span class="lx">{{list.class_name}}{{list.category}}</span>
                </dd>
            </dl>
        </div>
    </div>
</template>
<script>
import Axios from 'axios'
    export default {
        data(){
            return{
                booklist:[],
                name:'',
            }
        },
        mounted () {
            let url = 'https://www.apiopen.top/novelApi';
            Axios.get(url).then(res=>{
                console.log(res);
                this.booklist = res.data.data;
                console.log(this.booklist);
            }).catch();
           
        },
        
        methods:{
            show(){
                 let url2 = 'https://www.apiopen.top/novelInfoApi?name='+this.name;
                 Axios.get(url2).then(res=>{
                this.booklist = res.data.data.data;
                console.log(this.booklist);
            }).catch();
           
            }
        }
    }
</script>

<style scoped>
#book{margin-bottom: 1rem;}
.serch{
    width:100%;
    height:100px;
    /* background:red; */
}
.serch>img{
    float:right;
    width:30px;
    margin-top:30px;
    margin-right:20px;
}
.serch>input{
    float:left;
    width:75%;
    height: 30px;
    margin-top: 30px;
    margin-left: 10px;
    border:none;
    outline: none;
    border:4px solid #ccc;
    box-shadow: #ccc
}
.book-list{
    width:100%;
    height:2.7rem;
    border-top:1px solid #ccc;
    margin-top:-0.2rem;
    /* margin-bottom:0.2rem; */
}
.woker{
    float:left;
    font-size:0.1rem;
    margin-top:0.2rem;
}
.book-size{
    font-size:0.1rem;
}
.xzzf{
    width:100%;
    height:0.8rem;

    overflow:hidden;
}
.lz{
    float:right;
    display:block;
    width:40px;
    height:20px;
    border-radius:40px;
    border:1px solid red;
    color:red;
    margin-top:0.2rem;
    font-size:0.1rem;
    margin-right:0.1rem;
}
.lx{
    float:right;
    display:block;
    width:65px;
    height:20px;
    border-radius:40px;
    border:1px solid #EECC8D;
    color:#EECC8D;
    margin-top:0.2rem;
    font-size:0.1rem;
    margin-right:0.1rem;
}
.book-list>dl>dt{
    width:22%;
    height:2.1rem;
    margin-left:.2rem;
    background:red;
    float:left;
    margin-top:0.2rem;
    
    
}
.book-list>dl>dt>img{
    width:100%;
    height:2.1rem;
}
.book-list>dl>dd{
    float:right;
    margin-right:0.2rem;
    width:68%;
    height:2.2rem;
    margin-left:0.1rem;
    margin-top:0.22rem;
    overflow:hidden;
}
</style>


