<template>
    <div class="list clearfix">
      <ul>
          <li v-for="(item,index) in photoList" :key= index>
              <router-link :to="'/photo/photoDetail/'+index">
                   <img :src="item.url" v-show="item.url">
              </router-link>
          </li>
      </ul>
    </div>
</template>
<script>
import Axios from 'axios'
export default {
    data(){
        return{
            photoList:[]
        }
    },
    mounted(){
        let url1 = 'https://www.apiopen.top/meituApi?page=1';
        Axios.get(url1).then(
            res =>{
                this.photoList = res.data.data;
                //将数据存入 vuex
            this.$store.dispatch('actionPhotoList', this.photoList);
                // console.log(this.photoList);
            }
        ).catch();
    }
}
</script>

<style scoped>
@import url('../../assets/css/common.css');
.list{ margin: 1rem 0;}
.list li{width: 50%; float:left;}
.list li img{width: 100%; height:180px;}
</style>



