<template>
    <div>
       <ul class="music-list">
           <li v-for="list in musiclist" :key="list.uid" v-if="list.type===a">
               <div class="movie" >
                   <img :src="list.header">
                   <span>{{list.username}}</span>
               </div>
               <div class="movie-text">
                   <p>{{list.text}}</p>
               </div>
               <!-- <div class="movie-img" v-if="list.image">
                   <router-link :to="'/music/MusicDetail'+list.uid">
                        <img  :src="list.image">
                   </router-link>
                      
               </div> -->
               <div v-if="list.video" >
                   <video :src="list.video" class="movie-video" controls></video>
               </div>
           </li>
       </ul>
    </div>
</template>
<script>
import Axios from "axios"; //引入Axios
export default {
    data(){
        return{
            musiclist:[],//得到数据
            a:"video",
        }
    },
    mounted(){
            let url1 ='./static/data/satinGodApi.json';//得到本地json数据 或者从网上拿到API接口
            Axios.get(url1).then(
                res =>{
                    this.musiclist = res.data.data;                 
                    // for(let i = 0; i<20; i++){
                    //      if(res.data.data[i].type!=this.a){
                    //      this.musiclist = res.data.data[i];
                    //     //  console.log(res.data.data[i].type!=this.a);
                    //     //  console.log(this.a);
                    //     console.log(res.data.data[i].type);
                        
                    // }
                   
                    // // console.log('a'+this.a);
                    // }
                    // console.log(res.data.data);
                    // console.log(this.musiclist);
                    console.log(this.musiclist);
                                    }
            ).catch(

            );
    },
}
</script>

<style scoped>
.music-list{
    margin: .9rem 0;
}
.movie{
 width: 100%;
 height: 40px;
 line-height: 40px;

}
.movie>img{
    width:8%;
    border-radius: 50%;
    float: left;
    margin-left: 5px;
    margin-top: 6px;
}
.movie>span{
    float: left;
    margin-left: 5px;
    font-size: .2rem;
    color: #ccc;
}
.movie-text>p{
    float: left;
    color: black;
    font-size: .3.9rem;
}
.movie-img{
    width: 100%;
    height: 100px;
    overflow: hidden;
}
.movie-video{
    width: 100%;
    height: 150px;
}
</style>



