<template>
    <div class="Header" :style="{background:bg}">
        <span class="home" @click="goHome">首页</span>
        <h2 class="tittle">娱乐</h2>
    </div>
</template>

<script>
    export default{
        methods:{
            goHome(){
                this.$router.push('/')
            }
          
        } ,
        props:['bg'],
    }
</script>

<style scoped>
    .Header{
        width: 100%;
        height: 1rem;
        background: #2196f3;
        position: fixed;
        top: 0;
        line-height: 1rem;
        z-index:9;
    }
    .home{
        color: #fff;
        margin-left: -5rem;
    }
     .tittle{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color:#fff;
    } 
</style>


